html {
  font-family: Roboto, Verdana, sans-serif;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
}

.card__reviews, .card__prices {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

.button {
  cursor: pointer;
  border: none;
  width: 100%;
  display: block;
}

img {
  width: 100%;
  height: 100%;
}

a {
  transition: all .3s;
}

.catalog {
  grid-template-columns: 20rem;
  place-content: center;
  gap: 4.6rem 4.8rem;
  padding: 5rem 4rem;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 20rem);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 20rem);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 20rem);
  }
}

.card {
  cursor: pointer;
  border: .1rem solid #f3f3f3;
  border-radius: .5rem;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 3.2rem 1.6rem 1.6rem;
  transition: all .3s;
  display: flex;
}

.card__name {
  margin-bottom: .4rem;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.8rem;
}

.card__image {
  width: 16rem;
  height: 13.4rem;
  margin-bottom: 4rem;
}

.card__name, .card__reviews, .card__price.value {
  color: #060b35;
}

.card__code, .card__prices {
  margin-bottom: 1.6rem;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4rem;
}

.card__code {
  color: #616070;
}

.card__reviews {
  margin-bottom: 2.4rem;
}

.card__review {
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4rem;
}

.card__price.text {
  color: #616070;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.8rem;
}

.card__price.value {
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.8rem;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: .1rem solid #00acdc;
  border-radius: .5rem;
  padding: 1.1rem 0;
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.6rem;
}

.card__button:hover {
  color: #00acdc;
  background-color: #0000;
}

.card:hover {
  scale: 1.2;
}

.card:hover .card__name {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  width: 1.6rem;
  height: 1.6rem;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars.stars--4 > .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 5rem;
  display: flex;
}

.logo__link {
  width: 4rem;
  height: 4rem;
  display: block;
}

.nav__list {
  margin: 0;
  padding-inline-start: 0;
  font-size: 1.2rem;
  display: flex;
}

.nav__item {
  margin: 0 1rem;
  list-style: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-content: center;
  height: 6rem;
  font-weight: 500;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link.is-active, .nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: .8rem;
  height: .4rem;
  margin-top: auto;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
