.header {
  display: flex;
  justify-content: space-between;
  padding: 0 5rem;
  align-items: center;
}

.logo__link {
  display: block;
  width: 4rem;
  height: 4rem;
}

.nav__list {
  display: flex;
  font-size: 1.2rem;
  padding-inline-start: 0;
  margin: 0;
}

.nav__item {
  list-style: none;
  margin: 0 1rem;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  display: block;
  align-content: center;
  position: relative;
  height: 6rem;
  text-decoration: none;
  color: $link-color;
  text-transform: uppercase;
  font-weight: 500;
}

.nav__link.is-active {
  color: $link-active-color;
}

.nav__link:hover {
  color: $link-active-color;
}

.nav__link.is-active::after {
  content: '';
  display: block;
  position: absolute;
  background-color: $link-active-color;
  height: 0.4rem;
  border-radius: 0.8rem;
  margin-top: auto;
  bottom: 0;
  left: 0;
  right: 0;
}
